.spaceComponent {
  display: flex;
  flex-direction: column;
  min-width: 450px;
  max-width: 500px;
  padding: 10px 0;
  align-items: center;
  overflow: auto;
}

.spaceImage {
  max-width: 100%;
}

.mainSpace {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.spacesSection {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}